import React from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import ReactGA from 'react-ga';
import Content from 'Content';
import BlogPost from 'BlogPost'
import PortfolioItem from 'PortfolioItem';
import CreateArticle from 'CreateArticle';

import './App.css';

ReactGA.initialize('UA-89551120-1', {testMode: process.env.NODE_ENV === 'test'});

// window.history.listen(location => );

(function () {
  var previousState = window.history.state;
  setInterval(function () {
    if (previousState !== window.history.state) {
      previousState = window.history.state;
      myCallback();
    }
  }, 100);
})();

function myCallback() {
  ReactGA.pageview(window.location.pathname + window.location.search);
}

class App extends React.Component {
  componentDidMount() {
    ReactGA.pageview(window.location.pathname + window.location.search);
  }

  render() {
    return (      
      <HashRouter>
        <div className="App wrapper container">
          <Switch>
            <Route exact path="/create/article" component={CreateArticle} />
            <Route exact path="/article/:slug" component={BlogPost} />
            <Route exact path="/portfolio/:slug" component={PortfolioItem} />
            <Route path="/" component={Content} />
          </Switch>
        </div>
      </HashRouter>
    );
  }
}

export default App;
